import { Graphics } from "../Graphics";
import { Looper, LooperParams } from "../Looper";
import { CannonDebugRenderer } from "./CannonDebug";
import { PGObject, PhysicsBodyCreator } from "./PhysicsBodyCreator";
import * as CANNON from "cannon-es";
import * as THREE from "three";
import { ModelUtils } from "./ModelUtils";
import { GLTF } from "../Loaders/GLTFLoader/GLTFLoader";
import { rm } from "fs";

export class Physics {

    private static instance: Physics | undefined = undefined;

    public constructor() {/** */ }

    private objects: PGObject[] = [];

    private world?: CANNON.World;

    private debug?: CannonDebugRenderer;

    public static getInstance = () => {
        if (undefined === this.instance) {
            this.instance = new Physics();
        }
        return this.instance;
    }

    private graphics?: Graphics;
    private scene?: THREE.Scene;

    public init = (graphics: Graphics) => {
        Physics.getInstance();
        this.graphics = graphics;
        this.scene = graphics.scene;

        //>> World
        this.world = new CANNON.World({
            gravity: new CANNON.Vec3(0, -9.82, 0), // m/s
        });

        //>> Debug
        this.debug = new CannonDebugRenderer(this.scene, this.world);

        //>> Loop
        Looper.getInstance().addListener(this.update);

        //>> Test
        Looper.getInstance().onFrameCount((params: LooperParams) => {
            // this.debug?.toggle();
            //console.log(`fps: ${1 / params.deltaTime}`)
        }, 25);



        return this;
    };

    public addPgObjects = (pgoArray: PGObject[]) => {
        pgoArray.map(o => {
            this.objects.push(o);
            this.scene?.add(o.mesh);
            this.world?.addBody(o.body);
        });

        return this;
    }


    private cleanMesh = (obj: THREE.Mesh | THREE.Group) => {
        const parent = obj.parent;
        parent?.remove(obj);

        obj.traverse(o => {
            const mesh = o as THREE.Mesh;
            mesh.geometry?.dispose();
            const mat = mesh.material as THREE.MeshStandardMaterial;
            mat?.map?.dispose();
            mat?.normalMap?.dispose();
            mat?.aoMap?.dispose();
            mat?.bumpMap?.dispose();
            mat?.alphaMap?.dispose();
            mat?.lightMap?.dispose();
            // other maps.
        });

        this.graphics?.renderer.renderLists.dispose();
        return this;
    }


    public removePgObjects = (pgoArray: PGObject[]) => {
        pgoArray.map(pgo => {
            console.log(`%c"${pgo.mesh.name}"%c is removed and disposed`, `color:#fc3`, `color:#888`);

            this.scene?.remove(pgo.mesh);
            this.world?.removeBody(pgo.body);
            this.objects.splice(this.objects.indexOf(pgo), 1);
            this.cleanMesh(pgo.mesh as THREE.Mesh);
        });

        return this;
    }

    private v3C2T = (v3: CANNON.Vec3) => {
        return new THREE.Vector3(v3.x, v3.y, v3.z);
    }

    private v4C2T = (v4: CANNON.Quaternion) => {
        return new THREE.Quaternion(v4.x, v4.y, v4.z, v4.w);
    }

    public update = (params: LooperParams) => {

        //>> Update the physics world
        this.world?.fixedStep(params.deltaTime);

        //>> Array of objects to be removed
        const rmObjs: PGObject[] = [];

        //>> Update objects position and rotation
        this.objects.map(pgo => {
            pgo.mesh.position.copy(this.v3C2T(pgo.body.position));
            pgo.mesh.quaternion.copy(this.v4C2T(pgo.body.quaternion));

            //>> Find objects to be removed
            const p = pgo.mesh.position;
            const D = 20;
            if (Math.abs(p.x) > D || Math.abs(p.y) > D || Math.abs(p.z) > D) {
                rmObjs.push(pgo);
            }
        });

        //>> Remove objects
        this.removePgObjects(rmObjs);

        //>> Update the debugger
        this.debug?.update();
    }

    public createPoFromGLTF = (glTF: GLTF, sceneIndex: number = 0) => {

        const poc = PhysicsBodyCreator.getInstance();

        //>> Physics object of MMO
        const mmoArray = poc.createPoFromMmo(glTF.scenes[sceneIndex]);

        //>> Physics object of SMO
        const smoArray = poc.createPoFromSmo(glTF.scenes[sceneIndex]);

        //>> Physic objects
        const pgoArray = [...mmoArray, ...smoArray];

        return pgoArray;
    }

    public buildPhysics = (glTF: GLTF, sceneIndex: number = 0) => {
        const pgoArray = this.createPoFromGLTF(glTF, sceneIndex);
        this.addPgObjects(pgoArray);
        return this;
    }

    public getDebugger = () => this.debug;
}