import { THREE } from "../../ThreeJS";

export interface TextureInfo {
    path: string;
    size: number;
    sceneNames: string[];
}

export interface TextureData {
    texture: THREE.Texture;
    info: TextureInfo;
}

export interface TextureDownloadInfo {
    path: string;
    percent: number;
    loaded: number;
    total: number;
}

export interface CubeTextureDownloadInfo {
    url: string;
    loaded: number;
    total: number;
    tag: string;
}

export interface CubeTextureDownloaderCallbacks {
    onStart?: (info?: string) => void;
    onProgress?: (info: CubeTextureDownloadInfo) => void;
    onDownloaded?: (cubeText: THREE.CubeTexture) => void;
    onError?: (error: string, url?: string) => void;
}

export class TextureLoader {
    private static instance?: TextureLoader;

    private constructor() {
        //
    }

    public static getInstance = () => {
        if (this.instance === undefined) {
            this.instance = new TextureLoader();
        }
        return this.instance;
    };

    private _checkName = (name: string) => {
        const names = ["bridge", "park", "snow"];
        let matchCount = 0;
        for (let i = 0; i < names.length; i++) {
            if (names[i] === name) {
                matchCount += 1;
                break;
            }
        }
        if (matchCount === 0) {
            throw new Error(`** The environment cube map "${name}" not found! **`);
        }
    };

    public loadEnvMap(root: string, name: "bridge" | "park" | "snow" | string, callbackOptions?: CubeTextureDownloaderCallbacks): Promise<THREE.CubeTexture> {
        // name: "show", "bridge", "park", etc.
        this._checkName(name);
        const names = ["posx.jpg", "negx.jpg", "posy.jpg", "negy.jpg", "posz.jpg", "negz.jpg"];
        return this.loadCubeEnvTexture(`${root}/${name}/`, names, callbackOptions);
    }

    public loadCubeEnvTexture(path: string, names: string[], callbackOptions?: CubeTextureDownloaderCallbacks): Promise<THREE.CubeTexture> {
        // console.log(`@loadCubeEnvTexture root: ${path}`);

        //>> Helper function
        const getTag = (url: string) => {
            return url.includes("park") ? "park" : url.includes("bridge") ? "bridge" : url.includes("snow") ? "snow" : "unknown";
        };

        return new Promise((resolve, reject) => {
            new THREE.CubeTextureLoader(
                new THREE.LoadingManager(
                    () => {
                        callbackOptions?.onStart?.(`start downloading ${names.length} files from "${path}"`);
                    },

                    (url: string, loaded: number, total: number) => {
                        /**
                         * loaded: number of files loaded
                         * total: number of files to be downloaded
                         */
                        //console.log(`%c${loaded}/${total} - ${url}`, `color: ${url.includes('park') ? '#8f8' : url.includes('bridge') ? '#88f': '#ff8'}`)
                        const tag = getTag(url);
                        const info: CubeTextureDownloadInfo = { url, loaded, total, tag };
                        callbackOptions?.onProgress?.(info);
                    },

                    (url: string) => {
                        const errMsg = `Error loading ${url}`;
                        console.error(errMsg);
                        reject(errMsg);
                        callbackOptions?.onError?.(errMsg, url);
                    },
                ),
            )
                .setPath(path)
                .load(
                    names,
                    (cubeText: THREE.CubeTexture) => {
                        cubeText.name = getTag(path);
                        resolve(cubeText);
                        callbackOptions?.onDownloaded?.(cubeText);
                        cubeText.dispose();
                    },
                    (xhr: any) => {
                        xhr;
                        console.log(xhr.loaded);
                    },
                );
        });
    }

    public loadDefaultCubeEnvTexture(dir: "bridge" | "park" | "snow" | string, callbackOptions?: CubeTextureDownloaderCallbacks): Promise<THREE.CubeTexture> {
        // dir: "show", "bridge", "park", etc.
        const names = ["posx.jpg", "negx.jpg", "posy.jpg", "negy.jpg", "posz.jpg", "negz.jpg"];
        //>> Vite
        return this.loadCubeEnvTexture(`/static/textures/cube/${dir}/`, names, callbackOptions);

        //>> SvelteKit
        // return this.loadCubeEnvTexture(`textures/cube/${dir}/`, names, callbackOptions);
    }
}
