import * as CANNON from "cannon-es";
import * as THREE from "three";
import { BodyType, Quaternion, Vec3 } from "cannon-es";
import { Box3, Group, Mesh, Object3D } from "three";
import { CannonUtils } from "./CannonUtils";

export type ModelShape = "plane" | "wall" | "ground" | "platform" | "floor" | "cube" | "box" | "sphere" | "ball" | "cylinder" | "cone" | "icosphere" | "torus" | "capsule" | "suzanne" | "player";


export class SMOBodyCreator {

    private cylinderNumSegments: number = 12;

    private forceIcosphereAsSphere: boolean = true;

    private forceUnknownAsCube: boolean = true;


    private static instance?: SMOBodyCreator = undefined;

    public static getInstance = () => {
        if (this.instance === undefined) {
            this.instance = new SMOBodyCreator();
        }
        return this.instance;
    }


    /*******************************************************************************************
     * Mesh Utility Functions
     *******************************************************************************************/

    public assertNoBounding(mesh: THREE.Mesh) {
        console.assert(mesh.geometry.boundingBox, `BoundingBox of the "${mesh.name}" is not defined.`);
        return !!mesh.geometry.boundingBox;
    }

    public getMeshOrigin(mesh: THREE.Mesh, retCannon = false): THREE.Vector3 | CANNON.Vec3 {
        // this.assertNoBounding(mesh);

        if (mesh.geometry.boundingBox === null) {
            mesh.geometry.computeBoundingBox();
        }

        const origin = new THREE.Vector3();
        const bb = mesh.geometry.boundingBox;
        if (bb) {
            origin.x = bb.min.x + (bb.max.x - bb.min.x) / 2;
            origin.y = bb.min.y + (bb.max.y - bb.min.y) / 2;
            origin.z = bb.min.z + (bb.max.z - bb.min.z) / 2;
        }

        if (retCannon === true) {
            return new CANNON.Vec3(origin.x, origin.y, origin.z);
        }
        return origin;
    }

    public getSphereRadius = (mesh: THREE.Mesh): number => {
        if (mesh.geometry.boundingSphere === null) {
            mesh.geometry.computeBoundingSphere();
        }
        const r = mesh.geometry.boundingSphere;
        // console.log(`@getSphereRadius r: ${r?.radius}`);
        if (r) {
            return r.radius;
        } else {
            throw new Error('The provided mesh not contains the "boundingSphere" property.!');
        }
    };

    public getBoundingBox(object: Object3D) {
        const bb: Box3[] = [];

        if (object.type === "Mesh") {
            const mesh = object as Mesh;
            if (mesh.geometry.boundingBox) bb.push(mesh.geometry.boundingBox);
        } else if (object.type === "Group") {
            const group = object as Group;
            group.children.forEach((c) => {
                if (c.type === "Mesh") {
                    const m = c as Mesh;
                    if (m && m.geometry.boundingBox) {
                        bb.push(m.geometry.boundingBox);
                    }
                }
            });
        } else {
            throw new Error("getBoundingBox supports only Mesh and Group!");
        }

        const _max = (a: number, b: number): number => (a > b ? a : b);
        const _min = (a: number, b: number): number => (a < b ? a : b);

        let xMax = 0;
        let xMin = 0;
        let yMax = 0;
        let yMin = 0;
        let zMax = 0;
        let zMin = 0;

        bb.map((b, i) => {
            xMax = _max(xMax, b.max.x);
            xMin = _min(xMin, b.min.x);

            yMax = _max(yMax, b.max.y);
            yMin = _min(yMin, b.min.y);

            zMax = _max(zMax, b.max.z);
            zMin = _min(zMin, b.min.z);
        });

        return new CANNON.Vec3(xMax - xMin, yMax - yMin, zMax - zMin);
    }

    public computeMass(object: THREE.Object3D) {
        const bb = this.getBoundingBox(object);
        return (bb.x * bb.y * bb.z) / 3;
    }

    public getPosition(object: THREE.Mesh | THREE.Object3D) {
        const pos = object.position;
        return new Vec3(pos.x, pos.y, pos.z);
    }

    public getQuaternion(object: THREE.Mesh | THREE.Object3D) {
        const qua = object.quaternion;
        return new Quaternion(qua.x, qua.y, qua.z, qua.w);
    }

    public copyPosition = (body: CANNON.Body, object: THREE.Mesh | THREE.Object3D) => {
        body.position.set(object.position.x, object.position.y, object.position.z);
    };

    public copyQuaternion = (body: CANNON.Body, object: THREE.Mesh | THREE.Object3D) => {
        body.quaternion.set(object.quaternion.x, object.quaternion.y, object.quaternion.z, object.quaternion.w);
    };

    /**
     * Copy Position and Quaternion of CANNON.Body to THREE.Mesh|THREE.Group
     */
    public copyPositionAndQuaternion(body: CANNON.Body, object: THREE.Mesh | THREE.Group) {
        this.copyPosition(body, object);
        this.copyQuaternion(body, object);
    }

    /*******************************************************************************************
     * BODY and SHAPE CREATORS
     /******************************************************************************************/

    /**
     * Create physics body based on the given types.
     * If the body type is dynamic, the mess is set to 1. The mess can be changed later.
     */
    public createCannonBody(type: BodyType = CANNON.Body.DYNAMIC, mass = 1) {
        const body = new CANNON.Body({ mass: mass });
        body.type = type;

        if (type === CANNON.Body.STATIC) {
            body.mass = 0;
        }
        else if (type === CANNON.Body.KINEMATIC) {
            body.velocity.setZero();
            body.angularVelocity.setZero();
        }
        else {
            // Dynamic body
        }

        body.updateMassProperties();
        body.aabbNeedsUpdate = true;
        return body;
    }

    /**
     * CUBE SHAPE
     */
    public createCubeShape(mesh: THREE.Mesh): CANNON.Box {
        const halfExt = this.getBoundingBox(mesh).scale(0.5);

        const shape = new CANNON.Box(halfExt);
        return shape;
    }

    /**
     * CUBE BODY
     */
    public createCubeBody(mesh: THREE.Mesh, mass?: number): CANNON.Body {
        const shape = this.createCubeShape(mesh);
        const body = new CANNON.Body({ mass: mass ? mass : this.computeMass(mesh) });
        this.copyPositionAndQuaternion(body, mesh);
        body.addShape(shape, this.getMeshOrigin(mesh) as CANNON.Vec3);
        return body;
    }

    /**
     * SPHERE SHAPE
     */
    public createSphereShape(mesh: THREE.Mesh): CANNON.Sphere {
        /**
         * The bounding box are (-1,-1,-1) and (+1,+1,+1)
         * The diagonal is Math.sqrt(2+2+2) = 3.461
         * The half the diagonal = radius of the bounding sphere = 1.7320499128373052.
         * REF: REF: https://forum.babylonjs.com/t/dimensions-of-boundingsphere-of-sphere-mesh-dont-match/14992/5
         */

        // ORG:
        const radius = this.getSphereRadius(mesh) / 1.7320499128373052;
        // console.log(`radius: ${radius}`)
        const shape = new CANNON.Sphere(radius);
        return shape;


        // NEW:
        // const radius = this.getSphereRadius(mesh);
        // const shape = new CANNON.Sphere(radius);
        // console.log(`radius: ${radius}`)
        // return shape;
    }

    /**
     * SPHERE BODY
     */
    public createSphereBody(mesh: THREE.Mesh, mass?: number): CANNON.Body {
        const shape = this.createSphereShape(mesh);
        const body = new CANNON.Body({ mass: mass ? mass : this.computeMass(mesh) });
        this.copyPositionAndQuaternion(body, mesh);
        body.addShape(shape, this.getMeshOrigin(mesh, true) as CANNON.Vec3); //body.addShape(shape)
        return body;
    }

    /**
     * CYLINDER SHAPE
     */
    public createCylinderShape(mesh: THREE.Mesh, numSegments: number = this.cylinderNumSegments): CANNON.Cylinder {
        /**
         * This function supports only normal shape of cylinder.
         * The top and bottom radiuses have the same value.
         */
        const bb = this.getBoundingBox(mesh).scale(0.5);
        const radiusTop = bb.x;
        const radiusBottom = bb.z;
        const height = bb.y * 2;
        const shape = new CANNON.Cylinder(radiusTop, radiusBottom, height, numSegments);

        return shape;
    }

    /**
     * Torus shape.
     * The shape of Torus is a simple Cylinder shape.
     */
    public createTorusShape(mesh: THREE.Mesh) {
        const shape = this.createCylinderShape(mesh);
        return shape;
    }

    /**
     * Capsule shape.
     * The shape of Capsule is a simple Cylinder shape.
     */
    public createCapsuleShape(mesh: THREE.Mesh) {
        const dims = this.getBoundingBox(mesh);
        const capsuleHeight = dims.y;
        const capsuleRadius = dims.x / 2; // x and z should be the same.
        const shape = new CANNON.Cylinder(capsuleRadius, capsuleRadius, capsuleHeight, this.cylinderNumSegments);
        return shape;
    }

    /**
     * Suzanne shape.
     * The shape of Suzanne is a simple Cylinder shape.
     */
    public createSuzanneShape(mesh: THREE.Mesh) {
        const dims = this.getBoundingBox(mesh);
        const capsuleHeight = dims.y;
        const capsuleRadius = dims.x / 2; // x and z should be the same.
        const shape = new CANNON.Cylinder(capsuleRadius, capsuleRadius, capsuleHeight, this.cylinderNumSegments);
        return shape;
    }

    public createSpecialCylinderShape(mesh: THREE.Mesh, radiusTopRatio = 0.5, radiusBottomRatio = 1, numSegments: number = this.cylinderNumSegments): CANNON.Cylinder {
        /**
         * This function supports only normal shape of cylinder.
         * The top and bottom radiuses have the same value.
         * The radiusTopRatio and radiusBottomRatio are turning parameters.
         */
        const bb = this.getBoundingBox(mesh).scale(0.5);
        const radiusTop = bb.x * radiusTopRatio;
        const radiusBottom = bb.z * radiusBottomRatio;
        const height = bb.y * 2;
        const shape = new CANNON.Cylinder(radiusTop, radiusBottom, height, numSegments);
        return shape;
    }

    /**
     * CYLINDER BODY
     */
    public createCylinderBody(mesh: THREE.Mesh, mass?: number): CANNON.Body {
        const shape = this.createCylinderShape(mesh);
        const body = new CANNON.Body({ mass: mass ? mass : this.computeMass(mesh) });
        this.copyPositionAndQuaternion(body, mesh);
        body.addShape(shape, this.getMeshOrigin(mesh, true) as CANNON.Vec3);

        return body;
    }

    /**
     * CYLINDER BODY
     */
    public createSpecialCylinderBody(mesh: THREE.Mesh, radiusTopRatio = 0.5, radiusBottomRatio = 1, mass?: number): CANNON.Body {
        const shape = this.createSpecialCylinderShape(mesh, radiusTopRatio, radiusBottomRatio);
        const body = new CANNON.Body({ mass: mass ? mass : this.computeMass(mesh) });
        this.copyPositionAndQuaternion(body, mesh);
        body.addShape(shape, this.getMeshOrigin(mesh, true) as CANNON.Vec3);
        return body;
    }

    /**
     * CONE BODY (It is a special type of Cylinder)
     */
    public createConeBody(mesh: THREE.Mesh, mass?: number): CANNON.Body {
        /**
         * The Cone is a special type of the Cylinder with top radius closed to 0.
         */
        const radiusTop = 0.001;
        const shape = this.createSpecialCylinderShape(mesh, radiusTop, 1);
        const body = new CANNON.Body({ mass: mass ? mass : this.computeMass(mesh) });
        this.copyPositionAndQuaternion(body, mesh);
        body.addShape(shape, this.getMeshOrigin(mesh, true) as CANNON.Vec3);
        return body;
    }

    /**
     * CANNON Plane Shape. (Cannon specific)
     */
    public createCannonPlaneShape(): CANNON.Plane {
        const shape = new CANNON.Plane();
        return shape;
    }

    /**
     * CANNON Plane Body (Cannon specific)
     */
    public createCannonPlaneBody(mass = 0): CANNON.Body {
        /**
         * When the mess of the body equals to 0, the body acts as static object.
         */
        const shape = this.createCannonPlaneShape();
        const body = new CANNON.Body({ mass: mass });
        body.quaternion.setFromAxisAngle(new CANNON.Vec3(1, 0, 0), -Math.PI / 2);
        body.addShape(shape);

        return body;
    }

    /**
     * Plane Shape.
     */
    public createPlaneShape(mesh: THREE.Mesh): CANNON.Box {
        const halfExt = this.getBoundingBox(mesh).scale(0.5);
        /**
         * The thickness of a plane is zero. Add a small thickness to it to be sure the physics works correctly.
         */

        if (halfExt.y <= 0) halfExt.y = 0.01;
        const shape = new CANNON.Box(halfExt);

        return shape;
    }

    /**
     * Plane Body.
     */
    public createPlaneBody(mesh: THREE.Mesh, mass = 0): CANNON.Body {
        /**
         * When the mess of the body equals to 0, the body acts as static object.
         */
        const shape = this.createPlaneShape(mesh);
        const body = new CANNON.Body({ mass: mass });
        this.copyPositionAndQuaternion(body, mesh);
        body.addShape(shape, this.getMeshOrigin(mesh, true) as CANNON.Vec3);
        return body;
    }

    /**
     * TRIMESH
     */
    public createTrimeshShape(mesh: THREE.Mesh) {
        const shape = CannonUtils.CreateTrimesh(mesh.geometry);
        return shape;
    }

    public createTrimeshBody(mesh: THREE.Mesh, mass?: number) {
        const shape = CannonUtils.CreateTrimesh(mesh.geometry);
        const body = new CANNON.Body({ mass: mass ? mass : this.computeMass(mesh) });
        this.copyPositionAndQuaternion(body, mesh);
        body.addShape(shape, this.getMeshOrigin(mesh, true) as CANNON.Vec3);
        return body;
    }

    /**
     * ICOSAHEDRON (not works with the Icosphere exported from Blender)
     */
    public createIcosahedronShape(mesh: THREE.Mesh) {
        const shape = CannonUtils.CreateConvexPolyhedron(mesh.geometry);
        return shape;
    }

    public createIcosahedronBody(mesh: THREE.Mesh, mass?: number) {
        const shape = CannonUtils.CreateConvexPolyhedron(mesh.geometry);
        const body = new CANNON.Body({ mass: mass ? mass : this.computeMass(mesh) });
        this.copyPositionAndQuaternion(body, mesh);
        body.addShape(shape, this.getMeshOrigin(mesh, true) as CANNON.Vec3);
        return body;
    }

    public createCapsuleBodyMultiShapes(mesh: THREE.Mesh, mass?: number) {

        const dims = this.getBoundingBox(mesh);
        const capsuleRadius = dims.x / 2; // x and z should be the same.
        const capsuleHeight = dims.y - 2 * capsuleRadius;

        const cylinderShape = new CANNON.Cylinder(capsuleRadius, capsuleRadius, capsuleHeight, this.cylinderNumSegments);

        const topSphereShape = new CANNON.Sphere(capsuleRadius);
        const bottomSphereShape = new CANNON.Sphere(capsuleRadius);

        const body = new CANNON.Body({ mass: mass ? mass : this.computeMass(mesh) });

        const origin: CANNON.Vec3 = this.getMeshOrigin(mesh, true) as CANNON.Vec3;

        /**
         * Fine turning for position of the top and bottom shapes (spheres).
         */
        const yOffset = 0;
        const offset = new CANNON.Vec3(0, yOffset, 0);

        const topPos = origin.vadd(new CANNON.Vec3(0, +capsuleHeight / 2, 0));
        const botPos = origin.vadd(new CANNON.Vec3(0, -capsuleHeight / 2, 0));
        body.addShape(cylinderShape, origin);
        body.addShape(topSphereShape, topPos.vadd(offset));
        body.addShape(bottomSphereShape, botPos.vsub(offset));

        this.copyPositionAndQuaternion(body, mesh);

        return body;
    }

    public createSuzanneBodyMultiShapes(mesh: THREE.Mesh, mass?: number) {
        const dims = this.getBoundingBox(mesh);
        const suzanneRadius = dims.x / 2; // x and z should be the same.

        const headSphereShape = new CANNON.Sphere(suzanneRadius * 0.62);
        const chinSphereShape = new CANNON.Sphere(suzanneRadius * 0.2);
        const earLSphereShape = new CANNON.Sphere(suzanneRadius * 0.25);
        const earRSphereShape = new CANNON.Sphere(suzanneRadius * 0.25);

        const body = new CANNON.Body({ mass: mass ? mass : this.computeMass(mesh) });

        const origin: CANNON.Vec3 = this.getMeshOrigin(mesh, true) as CANNON.Vec3;

        const K = suzanneRadius;

        // Head
        const headPos = new CANNON.Vec3(0, 0.19 * K, 0.12 * K);
        body.addShape(headSphereShape, origin.vadd(headPos));

        // Chin
        const chinPos = new CANNON.Vec3(0, -0.5 * K, 0.4 * K);
        body.addShape(chinSphereShape, origin.vadd(chinPos));

        // Ear Left
        const earLPos = new CANNON.Vec3(0.8 * K, 0.15 * K, -0.32 * K);
        body.addShape(earLSphereShape, origin.vadd(earLPos));

        // Ear Right
        const earRPos = new CANNON.Vec3(-0.8 * K, 0.15 * K, -0.32 * K);
        body.addShape(earRSphereShape, origin.vadd(earRPos));

        this.copyPositionAndQuaternion(body, mesh);

        return body;
    }

    public makeStaticBody(body: CANNON.Body) {
        body.type = CANNON.Body.STATIC;
        body.mass = 0;
        body.updateMassProperties();
        body.aabbNeedsUpdate = true;
        return body;
    }

    /*************************************************************************************/
    /*                                                                                   */
    /*************************************************************************************/

    public isModelOf = (name: string, ModelShape: ModelShape) => {
        name = name.toLowerCase();
        return name.indexOf(ModelShape) === 0;
    }


    public getInferredType(mesh: THREE.Mesh) {
        const name = mesh.name.toLowerCase();
        if (this.isModelOf(name, "plane") || this.isModelOf(name, "floor") || this.isModelOf(name, "ground")) {
            return CANNON.Body.STATIC;
        } else if (this.isModelOf(name, "player")) {
            return CANNON.Body.KINEMATIC;
        }

        return CANNON.Body.DYNAMIC;
    }

    public createInferredShape(mesh: THREE.Mesh) {
        const name = mesh.name.toLowerCase();

        let shape: CANNON.Shape;

        //console.log(`%c ${name} ${Mdu.isPlane(name)} ${Mdu.isFloor(name)}`, `color: red`)

        if (this.isModelOf(name, "plane") || this.isModelOf(name, "floor") || this.isModelOf(name, "ground")) {
            shape = this.createPlaneShape(mesh);
        }
        else if (this.isModelOf(name, "cube")) {
            shape = this.createCubeShape(mesh);
        }
        else if (this.isModelOf(name, "sphere")) {
            shape = this.createSphereShape(mesh);
        }
        else if (this.isModelOf(name, "cylinder")) {
            return this.createCylinderShape(mesh);
        }
        else if (this.isModelOf(name, "cone")) {
            shape = this.createSpecialCylinderShape(mesh, 0.001, 1);
        }
        else if (this.isModelOf(name, "torus")) {
            shape = this.createTorusShape(mesh);
        }
        else if (this.isModelOf(name, "capsule")) {
            shape = this.createCapsuleShape(mesh);
        }
        else if (this.isModelOf(name, "suzanne")) {
            shape = this.createSuzanneShape(mesh);
        }
        else if (this.isModelOf(name, "icosphere")) {
            /**
             * WARNING: Icosphere exported from blender is not Icosahedron. The `createIcosahedronBody` not works.
             */
            if (this.forceIcosphereAsSphere === true) {
                console.log(`%cThe shape of "${name}" will be a sphere-shape.`, "color:yellow");
                shape = this.createSphereShape(mesh);
            }
            else {
                shape = this.createTrimeshShape(mesh);
            }
        } else {
            /**
             * For unknown objects
             */

            if (this.forceUnknownAsCube === true) {
                /**
                 *
                 */
                console.log(`%cThe unknown object "${mesh.name}" will be applied by box-shape.`, "color:orange");
                shape = this.createCubeShape(mesh);
            } else {
                /**
                 * WARNING: The TrimeshBody can collide to the CANNON.Plan and CANNON.Sphere.
                 */
                console.log(`%cThe unknown object "${mesh.name}" will be applied by Trimesh-shape.`, "color:orange");
                shape = this.createTrimeshShape(mesh);
            }
        }

        return shape;
    }

    /**
     * Create body and its shape(s).
     * This function create physics body and shape(s) based on the name of given object.
     */
    public createInferredBody(object: THREE.Mesh, type?: CANNON.BodyType, mass?: number): CANNON.Body {
        return this.createBody(object, type, mass);
    }

    /**
     * Create Body from mesh.
     */
    public createBody(object: THREE.Mesh, type?: CANNON.BodyType, mass?: number): CANNON.Body {
        /**
         * By default, the body type is DYNAMIC
         */
        type = type ? type : CANNON.Body.DYNAMIC;
        mass = mass ? mass : this.computeMass(object);

        /**
         * Create a body with the specified mass.
         */
        let body: CANNON.Body;


        /**
         * Capsule and Suzanne are made of multiple shapes.
         */

        if (this.isModelOf(object.name, "capsule")) {
            // Create shapes and attach them to the body.
            body = this.createCapsuleBodyMultiShapes(object, mass);
        }

        else if (this.isModelOf(object.name, "suzanne")) {
            // Create shapes and attach them to the body.
            body = this.createSuzanneBodyMultiShapes(object, mass);
        }

        /**
         * Single shape object
         */
        else {
            //  Create a body
            body = new CANNON.Body({ mass: mass });

            // Create a shape based on the name of object.
            const shape = this.createInferredShape(object);

            // Add the shape to the body with the offset (origin of mess modified by Blender).
            body.addShape(shape, this.getMeshOrigin(object, true) as CANNON.Vec3);

            // Copy the position and quaternion of the object to the body.
            this.copyPositionAndQuaternion(body, object);
        }


        /**
         * Convert the Plane and Floor types to STATIC
         */
        const name = object.name.toLowerCase();
        if (this.isModelOf(name, "plane") || this.isModelOf(name, "floor") || this.isModelOf(name, "ground")) {
            this.makeStaticBody(body); // Convert to STATIC BODY -> type=CANNON.Body.STATIC and mass=0
            console.log(`%cThe object "${object.name}" is converted to STATIC BODY.`, "color:orange");
        }
        else {
            body.type = type; // DYNAMIC or KINEMATIC
        }

        /**
         * Check if the type is defined by Blender?
         * In Blender, the userData can be defined in the Object Data -> Custom Properties
         */
        if (object.userData.type) {
            const type = object.userData.type.toLowerCase();
            if (type === "static") {
                this.makeStaticBody(body);
            }
            else if (type === "dynamic" || type === "rigid") {
                body.type = CANNON.Body.DYNAMIC;
            }
            else if (type === "kinematic") {
                body.type = CANNON.Body.KINEMATIC;
            }
            else {
                console.log(`%cThe type "${object.userData.type}" provided in the "userData.type" is ignored`, "color:orange");
            }
        }

        return body;
    }

    public createPhysicBody(object: THREE.Mesh, type?: CANNON.BodyType, mass?: number): CANNON.Body {
        return this.createInferredBody(object, type, mass);
    }
}
