import { Browser, Size } from "./Browser";
import { EventBus } from "./EventBus";
import { DevLog } from "./Panel/DevLog";


/**
 * This class is used to stack canvas elements both 2D and 3D contexts.
 * All canvas elements will be resize to fit to the container.
 * ALl canvas elements should be set to `absolute`
 */

/*
Example:
    const stkContainer = new StackContainer("container1");
    const graphics = new Graphics(stkContainer);
*/


export class StackContainer {

    private static MIN_WIDTH = 32;
    private static MIN_HEIGHT = 32;

    private container: HTMLElement;

    private eventBus: EventBus = EventBus.getInstance();

    constructor(container?: HTMLElement | string) {

        if (container === undefined) {
            throw new Error(`${container} is not allowed to be the container!`);
        }

        if (typeof container === "string") {
            this.container = document.getElementById(container) as HTMLElement;
            if (this.container === null) {
                throw new Error(`The container id "${container}" is not found!`);
            }
        }
        else {
            this.container = container as HTMLElement;
        }

        if (container === null || container === undefined) {
            throw new Error("No container is founded!");
        }

        this.container.classList.add("relative");
        this.container.classList.add("box-border");

        Browser.getInstance().onResize(this._onResizeCallback);

        this.autoSize();
    }

    public getContainer = () => this.container;

    public getStackedCanvases = () => Array.from(this.container.childNodes).filter(c => c.nodeName === "CANVAS");

    public onResized = (callback: (size: Size) => void) => {
        this.eventBus.register("stack-container-resize", callback);
    }

    /**
     * Resizes the container based on the `width` and `height` parameters.
     * However, when the browser is resized, the `autoSize` function is executed, and the container size will be resized based on the HTML and CSS.
     * Operations:
     * 1) Check the `width` and `height`, if they are too small, their size will be changed to the predefined value (32x32).
     * 2) Resize the container by assigning `width` and `height` to the `style.width` and `style.height`.
     * 3) Resize children elements (only canvas objects are considered).
     */

    public setSize = (width: number, height: number) => {

        //>> 1) Check minimum size
        if (width < StackContainer.MIN_WIDTH || height < StackContainer.MIN_HEIGHT) {
            DevLog.getInstance().log(`@StackContainer.setSize: ${width}x${height} is too small`);
            width = StackContainer.MIN_WIDTH;
            height = StackContainer.MIN_HEIGHT;
            DevLog.getInstance().log(`@StackContainer.setSize: Resize to ${width}x${height}`);
            DevLog.getInstance().log(`@StackContainer.setSize: It is better to specify the container size using css or call the setSize function to set the container size.`);
        }

        //>> 2) Container size
        const container = this.container as HTMLElement;
        container.style.width = `${width}px`;
        container.style.height = `${height}px`;

        //>> 3) Canvas size
        this._updateSizeOfChildCanvases(width, height);

        return this;
    };

    /**
     * Makes the container change its size based on the HTML/CSS layout.
     * Operations:
     * 1) Remove the values of `style.width` and `style.height`. This will allow the container to resize itself.
     * 2) Get the new size of the container and update `style.width` and `style.height` by calling the `setSize` function.
     */
    public autoSize = () => {

        //>> 1) Autosize
        const container = this.container as HTMLElement;
        container.style.width = ``;
        container.style.height = ``;

        //> 2) Take the size and write it back
        const rect = this.getComputedRect();
        this.setSize(rect.width, rect.height);

        return this;
    }

    public getComputedRect = () => {
        window.getComputedStyle(this.container);
        const rect = this.container.getBoundingClientRect();
        return rect;
    }


    /**
     * PRIVATE
     */

    private _updateSizeOfChildCanvases = (width: number, height: number) => {
        this.getStackedCanvases().map(c => {
            const canvas = c as HTMLCanvasElement;
            canvas.width = width;
            canvas.height = height;
        });
    }

    //>> Called when browser resized
    private _onResizeCallback = (size: Size) => {
        const rect = this.getComputedRect();
        this.setSize(rect.width, rect.height);
        this.eventBus.dispatch("stack-container-resize", { width: rect.width, height: rect.height });
    };
}
