import { GLTF } from "./Loaders/GLTFLoader/GLTFLoader";
import { ModelLoader, ModelLoaderCallbacks } from "./Loaders/GLTFLoader/ModelLoader";
import { CubeTextureDownloaderCallbacks, TextureLoader } from "./Loaders/Texture/TextureLoader";

const CUBE_DIR = `textures/cube`;
const MODEL_DIR = `models`;

const RESOURCES = [
	//>> Cube textures
	{
		type: "env",
		name: "park",
		uri: CUBE_DIR,
	},
	{
		type: "env",
		name: "bridge",
		uri: CUBE_DIR,
	},
	{
		type: "env",
		name: "snow",
		uri: CUBE_DIR,
	},

	//>> glTF models
	// {
	//     type: "gltf",
	//     name: "cube1.glb",
	//     uri: MODEL_DIR,
	// },
	{
		type: "gltf",
		name: "RobotArm.glb",
		uri: MODEL_DIR,
	},
	// {
	//     type: "gltf",
	//     name: "Conveyer.glb",
	//     uri: MODEL_DIR,
	// },
	// {
	//     type: "gltf",
	//     name: "ArmHydraulic.glb",
	//     uri: MODEL_DIR,
	// },
	// {
	// 	type: "gltf",
	// 	name: "primitive.glb",
	// 	uri: MODEL_DIR,
	// },

	//>> Textures
	// {
	//     type: "texture", name: ""
	// }
];


export type DevFramework = "Vite" | "SvelteKit";

export interface Resources {
	envMaps: THREE.CubeTexture[];
	textures: THREE.Texture[];
	glTFs: GLTF[];
}

export class ResourceLoader {

	private static instance?: ResourceLoader = undefined;

	private resources: Resources = { envMaps: [], textures: [], glTFs: [] };

	private resourceList = RESOURCES;

	private static devFrameWork?: DevFramework = undefined;

	private static staticDir: string = "/";


	private constructor() {
		/* */
	}

	public static getInstance(devFrameWork?: DevFramework): ResourceLoader {

		if (this.instance === undefined) {
			this.instance = new ResourceLoader();

			if (devFrameWork !== undefined) {
				ResourceLoader.updateRootDir(devFrameWork);
			}

		}
		return this.instance;
	}

	public static updateRootDir = (devFrameWork: DevFramework) => {
		ResourceLoader.devFrameWork = devFrameWork;
		ResourceLoader.staticDir = devFrameWork === "SvelteKit" ? "" : "/static";
		const colors = ["color:#8f8", "color:#88f", "color:#f88"];
		console.log(`%cframework: %c"${devFrameWork}" %cstatic directory: %c"${ResourceLoader.staticDir}"`, colors[0], colors[1], colors[0], colors[2]);
		return ResourceLoader;
	}

	public setFramework(devFrameWork: DevFramework) {
		ResourceLoader.updateRootDir(devFrameWork);
		return ResourceLoader;
	}

	public load = (cubeTextureDownloaderCallbacks?: CubeTextureDownloaderCallbacks, modelLoaderCallbacks?: ModelLoaderCallbacks): Promise<Resources> => {

		if (ResourceLoader.devFrameWork === undefined) {
			const msg = `** No DevFramework is specified.**\nResourceLoader.getInstance().setFramework("SvelteKit | Vite");\nmust be called first.`;
			alert(msg);
			throw new Error(msg);
		}

		return new Promise((resolve, reject) => {
			let cnt = 0;
			const _countAndResolve = () => {
				cnt += 1;
				if (cnt >= this.resourceList.length) {
					resolve(this.resources);
				}
			};

			this.resourceList.forEach((list) => {
				if (list.type === "env") {
					//console.log(`downloading ${list.name}`);
					TextureLoader.getInstance()
						.loadEnvMap(`${ResourceLoader.staticDir}/${list.uri}`, list.name, cubeTextureDownloaderCallbacks)
						.then((cubeTexture) => {
							this.resources.envMaps.push(cubeTexture);
							//console.log(`${list.name} is downloaded.`);
							_countAndResolve();
						})
						.catch(() => reject(`error downloading ${list.type}.${list.name}`));
				} else if (list.type === "gltf") {

					console.log(`downloading ${list.type}.${list.name}`);
					ModelLoader.getInstance()
						.load(`${ResourceLoader.staticDir}/${list.uri}/${list.name}`, modelLoaderCallbacks)
						.then((data) => {
							const glTf = data.glTF;
							this.resources.glTFs.push(glTf);
							_countAndResolve();
							//console.log(`${list.name} is downloaded.`);
							// console.log(`%c${list.name} contains ${glTf.scenes.length} scenes:`, `color: lime`)
							// glTf.scenes.forEach(scene => {
							//     console.log(`%c  + ${scene.name}`, `color: cyan`);
							// });
						})
						.catch(() => reject(`error downloading ${list.type}.${list.name}`));
				}
			});
		});
	};
}
