import { Point, Size } from "../Browser";

export class DevBox {

    public container: HTMLElement;

    private mouseDownPosition: Point = { x: 0, y: 0 };
    private boxPosition: Point = { x: 0, y: 0 };
    private boxSize: Size = { width: 0, height: 0 };

    constructor(parent?: HTMLElement) {

        this.container = document.createElement("div");
        this.container.id = `box`;

        this.container.className = `flex flex-row justify-center items-center rounded-md bg-black/50 absolute border-2 border-gray-500/40 cursor-move select-none p-1 box`;

        this.container.style.zIndex = "900";

        this.container.addEventListener("mousedown", this._onMouseDown);

        if (parent === undefined) {
            parent = document.body;
        }

        parent.appendChild(this.container);

        this.setPosition(2, 2);
    }

    public autoSize = () => {
        //>> Remove values to make it auto size
        this.container.style.width = "";
        this.container.style.height = "";

        //>> Update size
        this.getSize();
        return this;
    }

    public lockCurrentSize = (expandX: number = 5, expandY: number = 5) => {
        const size = this.getSize();
        this.setSize(size.width + expandX, size.height + expandY);
        return this;
    }

    public appendChild = (child: HTMLElement) => {
        this.container.appendChild(child);
        return this;
    }

    public setPosition = (x: number, y: number) => {
        this.container.style.left = `${x}px`;
        this.container.style.top = `${y}px`;
        return this;
    }

    public getPosition = () => {
        const bcr = this.container.getBoundingClientRect();
        return {
            x: bcr.left, y: bcr.top,
        }
    }

    public getSize = () => {
        const bcr = this.container.getBoundingClientRect();
        this.boxSize.width = bcr.width;
        this.boxSize.height = bcr.height;
        return { width: bcr.width, height: bcr.height };
    }

    public setSize = (width: number, height: number) => {
        this.boxSize.width = width;
        this.boxSize.height = height;
        this.container.style.width = `${width}px`;
        this.container.style.height = `${height}px`;
        return this;
    }

    private _onMouseDown = (e: MouseEvent) => {
        //>> Add Mouse-UP and Mouse-Move
        document.addEventListener("mouseup", this._onMouseUp);
        document.addEventListener("mousemove", this._onMouseMove);

        this.mouseDownPosition = { x: e.clientX, y: e.clientY }
        this.boxPosition = this.getPosition();
    }
    private _onMouseUp = (e: MouseEvent) => {
        //>> Remove Mouse-UP and Mouse-Move
        document.removeEventListener("mouseup", this._onMouseUp);
        document.removeEventListener("mousemove", this._onMouseMove);
    }
    private _onMouseMove = (e: MouseEvent) => {
        //>> Mouse Distance
        const dmx = e.clientX - this.mouseDownPosition.x;
        const dmy = e.clientY - this.mouseDownPosition.y;

        //>> Box position
        let px = this.boxPosition.x + dmx;
        let py = this.boxPosition.y + dmy;

        //>> Limit Left, Top
        px = px < 0 ? 0 : px;
        py = py < 0 ? 0 : py;

        //>> Limit Right, Bottom
        const dw = window.innerWidth - this.boxSize.width;
        const dh = window.innerHeight - this.boxSize.height;
        px = px > dw ? dw : px;
        py = py > dh ? dh : py;
        this.setPosition(px, py);
    }
}