/*

$SYS/broker/version                             // MQTT broker version
$SYS/broker/timestamp                           // Timestamp of the last status update
$SYS/broker/uptime                              // Broker uptime (in seconds)

$SYS/broker/clients/connected                   // List of currently connected clients
$SYS/broker/clients/disconnected                // List of recently disconnected clients
$SYS/broker/clients/total                       // Total number of clients connected
$SYS/broker/clients/maximum                     // Maximum number of clients connected since starting
$SYS/broker/clients/inactive                    // Number of inactive clients (not sending data)
$SYS/broker/clients/expired                     // Number of clients with expired subscriptions
$SYS/broker/clients/active                      // Number of active clients (currently sending data)
$SYS/broker/clients/duplicates                  // Number of clients with duplicate client IDs

$SYS/broker/store/messages/count                // Total number of messages currently stored by the broker
$SYS/broker/store/messages/bytes                // Total number of bytes used by the broker to store messages
$SYS/broker/store/messages/bytes/max            // Maximum number of bytes used by the broker to store messages

$SYS/broker/messages/sent                       // Total number of messages sent by the broker
$SYS/broker/messages/received                   // Total number of messages received by the broker
$SYS/broker/messages/stored                     // Total number of messages currently stored by the broker
$SYS/broker/messages/dropped                    // Total number of messages dropped by the broker

$SYS/broker/publish/messages/sent/1min          // Rate of messages sent by the broker (1-minute average)
$SYS/broker/publish/messages/received/1min      // Rate of messages received by the broker (1-minute average)

$SYS/broker/bytes/sent                          // Total number of bytes sent by the broker
$SYS/broker/bytes/received                      // Total number of bytes received by the broker

$SYS/broker/load/connections/1min               // Rate of new connections per minute
$SYS/broker/load/publish/dropped                // Number of publish messages dropped by the broker
$SYS/broker/load/bytes/sent/1min                // Rate of bytes sent by the broker (1-minute average)
$SYS/broker/load/bytes/received/1min            // Rate of bytes received by the broker (1-minute average)
$SYS/broker/load/messages/sent/1min             // Rate of messages sent by the broker (1-minute average)
$SYS/broker/load/messages/received/1min         // Rate of messages received by the broker (1-minute average)

$SYS/broker/heap/current                        // Current heap memory usage by the broker
$SYS/broker/heap/maximum                        // Maximum heap memory usage by the broker
$SYS/broker/heap/maximum-size                   // Maximum size of heap memory available to the broker

$SYS/broker/publish/messages/dropped            // Number of messages dropped by the broker due to various reasons
$SYS/broker/publish/messages/received           // Total number of messages received by the broker as a subscriber

*/


import { Client, Message } from 'paho-mqtt';

export { Client, Message } from 'paho-mqtt';

export interface MqttCallbackOptions {
    onConnectionSuccess?: (clientId: string, topic: string) => void;
    onConnectionFailure?: (clientId: string, event: Paho.MQTT.ErrorWithInvocationContext) => void;
    onConnectionLost?: (clientId: string, event: Paho.MQTT.MQTTError) => void;
    onMessageArrived?: (clientId: string, message: Message) => void;
}

export class MqttPahoClient {


    /**
    * Change this flag appropriately
    */
    private useSSL = true;      //<< Change this flag
    private isLocal = false;

    /**
     * Connection parameters
     */
    private port = this.useSSL ? 8081 : 8080;
    private protocol = this.useSSL ? "wss" : "ws";
    private host = this.isLocal ? `127.0.0.1` : `services.bits-fusion.com`;
    private brokerUrl = `${this.protocol}://${this.host}:${this.port}/mqtt`;

    /**
     * Note:
     * Paho-Mqtt uses WebSockets to connect to an MQTT Broker.
     */

    /**
     * Note:
     * For local host, the wss not works with `localhost`
     */



    private username = 'dtwin';

    private password = 'dtwin';

    private clientId = 'mqtt-client-id-' + (Math.random() * 10000).toFixed(0);

    private RECONNECT_TIMEOUT = 5000;

    private SELF_CHECK_TOPIC = `self-check`;

    private client: Client;

    callbacks?: MqttCallbackOptions;

    private selfTopic = this.clientId.replace(`mqtt-client-id-`, `self-topic/`);


    private _t0: number = 0;

    constructor(callbacks?: MqttCallbackOptions) {

        this.client = new Client(this.brokerUrl, this.clientId);
        this.callbacks = callbacks;
    }


    public connect = () => {

        return new Promise((resolve, reject) => {

            console.log(`%cConnecting to MQTT Broker... %c${this.host}:${this.port} (${this.useSSL ? `ws-SSL/TLS` : `ws`})`, `color:#58c`, `color:#ff5`);
            //
            this.client.onConnectionLost = (e: Paho.MQTT.MQTTError) => {
                this.logE(`Connection lost!`);
                this.logE(e.errorMessage);
                //
                this.callbacks?.onConnectionLost?.(this.clientId, e);
                setTimeout(() => this.client.connect(), this.RECONNECT_TIMEOUT);
            };

            this.client.onMessageArrived = (message: Message) => {
                // const c1 = `color:#8ff`;
                // const c2 = `color:#ff8`;
                // console.log(`%ctopic: %c${message.destinationName} %cmessage: #c${message.payloadString}`, c1, c2, c1, c2);

                /**
                 * Message
                 */
                //console.table({ topic: message.destinationName, message: message.payloadString });
                //

                //>> RTT: 50 - 100 ms
                if (message.destinationName === 'rtt') {
                    console.log(`rtt: ${(performance.now() - this._t0).toFixed(2)} ms - ${message.payloadString}`);
                }

                this.callbacks?.onMessageArrived?.(this.clientId, message);
            };



            /**
             * Try to connect to the Broker
             */
            this.client.connect({
                userName: this.username,
                password: this.password,
                useSSL: this.useSSL,
                //
                onSuccess: () => {
                    console.log(`%cConnected to MQTT Broker     %c${this.host}:${this.port} (${this.useSSL ? `ws-SSL/TLS` : `ws`})`, `color:#5f5`, `color:#ff5`);

                    //>> Subscribe to the SELF_CHECK_TOPIC
                    this.client.subscribe(this.SELF_CHECK_TOPIC);

                    //>> Publish this client id to the SELF_CHECK_TOPIC
                    const message = new Message(this.clientId);
                    message.destinationName = this.SELF_CHECK_TOPIC;
                    this.client.send(message);

                    //>> Execute the onConnectionSuccess callback if required
                    this.callbacks?.onConnectionSuccess?.(this.clientId, this.selfTopic);

                    //>>
                    // this.client.subscribe(`$SYS/broker/clients/connected`);


                    //>> $SYS
                    this.client.subscribe(`$SYS/broker/clients/connected`);
                    this.client.subscribe(`$SYS/broker/clients/disconnected`);
                    this.client.subscribe(`$SYS/broker/clients/maximum`);
                    this.client.subscribe(`$SYS/broker/uptime`);



                    //>> Test rtt (round trop time)
                    /*
                    this.client.subscribe(`rtt`);
                    setInterval(() => {
                        this._t0 = performance.now();
                        const message = new Message(`${Math.random() * 1000}`);
                        message.destinationName = `rtt`;
                        this._t0 = performance.now();
                        console.log(`sent...`)
                        this.client.send(message);
                    }, 1000);
                    */

                    resolve(this.clientId);

                },
                onFailure: (e: Paho.MQTT.ErrorWithInvocationContext) => {
                    this.logE(`Connection failure!`);
                    this.logE(e.errorMessage);
                    //
                    this.callbacks?.onConnectionFailure?.(this.clientId, e);
                    //
                    setTimeout(() => this.client.connect(), this.RECONNECT_TIMEOUT);
                },
            });
        });

    }

    public subscribe = (topic: string) => {
        this.client?.subscribe(topic);
        return this;
    }

    public publish = (topic: string, message: string) => {
        const mqttMessage = new Message(message);
        mqttMessage.destinationName = topic;
        this.client?.send(mqttMessage);
        return this;
    }

    public disconnect = () => {
        this.client?.disconnect();
    }

    private logE = (str: string) => {
        console.log(`%c${this.clientId}: %c${str}`, `color:#855`, `color:#f84`);
    }
    private logI = (str: string) => {
        console.log(`%c${this.clientId}: %c${str}`, `color:#855`, `color:#48f`);
    }
    private logS = (str: string) => {
        console.log(`%c${this.clientId}: %c${str}`, `color:#855`, `color:#4f8`);
    }

}

