import { THREE } from "./ThreeJS";
import { OrbitControls } from "./Controls/OrbitControls";
import { Size } from "./Browser";
import { Looper, LooperParams } from "./Looper";
import { CanvasUtils } from "./Canvas/CanvasUtils";
import { DevLog } from "./Panel/DevLog";
import { StackContainer } from "./StackContainer";

export class Graphics {

	public camera: THREE.PerspectiveCamera;

	public scene: THREE.Scene;

	public renderer: THREE.WebGLRenderer;

	public controls: OrbitControls;

	public topCanvas: HTMLCanvasElement;

	public wglCanvas: HTMLCanvasElement;

	public bkgCanvas: HTMLCanvasElement;

	public container: HTMLElement;

	private canvases: HTMLCanvasElement[] = [];

	private stackedContainer: StackContainer;

	constructor(container: StackContainer | string) {

		if (typeof container === "string") {
			this.stackedContainer = new StackContainer(container);
		}
		else {
			this.stackedContainer = container;
		}


		//>> Container
		this.container = this.stackedContainer.getContainer();

		//>> Canvases
		this.topCanvas = CanvasUtils.getInstance().create("top-canvas", 3, this.container);  	// 2D Overlay
		this.wglCanvas = CanvasUtils.getInstance().create("wgl-canvas", 2, this.container);		// Renderer
		this.bkgCanvas = CanvasUtils.getInstance().create("back-canvas", 1, this.container);	// 2D Underlay

		this.canvases.push(this.topCanvas);
		this.canvases.push(this.wglCanvas);
		this.canvases.push(this.bkgCanvas);

		//>> Graphics components
		this.camera = new THREE.PerspectiveCamera();
		this.scene = new THREE.Scene();
		this.renderer = new THREE.WebGLRenderer({
			canvas: this.wglCanvas,
			antialias: true,
		});
		this.controls = new OrbitControls(this.camera, this.container);

		this._initDefault();

		Looper.getInstance().addListener(this._onUpdateCallback);
	}

	//>> Called by the constructor
	private _initDefault = () => {

		//>> Renderer
		this.renderer.setClearAlpha(0);
		this.renderer.setClearColor(0x00000, 0);

		//>> Camera
		this.camera.position.set(0, 0, 5);
		this.camera.lookAt(new THREE.Vector3(0));

		//>> Directional light
		const light = new THREE.DirectionalLight(0xffffff, 2);
		light.position.set(5, 5, 5);
		this.scene.add(light);

		//>> Ambient light
		this.scene.add(new THREE.AmbientLight(0xffffff, 1));

		//>> Container dimension
		window.getComputedStyle(this.container);
		const rect = this.container.getBoundingClientRect();
		if (rect.width < 128 || rect.height < 128) {
			const DIM = 128;
			DevLog.getInstance().log(`@Graphics._initDefault: Dimension of container "${rect.width}x${rect.height}" is too small `);
			DevLog.getInstance().log(`@Graphics._initDefault: Dimension is changed to "${DIM}x${DIM}"`);
			this.setSize(DIM, DIM);
		}
		else {
			this.setSize(rect.width, rect.height);
		}

		this.stackedContainer.onResized((size: Size) => {
			this.autoSize();
		});
	};


	//>> Called by looper every frame
	private _onUpdateCallback = (info: LooperParams) => {
		info;
		this.renderer.render(this.scene, this.camera);
	};


	private _updateSizeOfRendererAndCamera = (width: number, height: number) => {
		//>> Renderer size
		this.renderer.setSize(width, height);
		this.renderer.setPixelRatio(width / height);

		//>> Camera aspect
		this.camera.aspect = width / height;
		this.camera.updateProjectionMatrix();
	}

	public setSize = (width: number, height: number) => {

		this.stackedContainer.setSize(width, height);
		this._updateSizeOfRendererAndCamera(width, height);
		return this;
	};

	/**
	 * The size is changed based on css or the size of children.
	 */
	public autoSize = () => {
		this.stackedContainer.autoSize();
		const rect = this.stackedContainer.getComputedRect();
		this._updateSizeOfRendererAndCamera(rect.width, rect.height);
		return this;
	}
}
