import * as THREE from "three";
import * as CANNON from "cannon-es";
import { ModelUtils } from "./ModelUtils";
import { MMOBodyCreator } from "./MMOBodyCreator";
import { SMOBodyCreator } from "./SMOBodyCreator";

/**
 * Physics and Graphics Object Interface
 */
export interface PGObject {
    mesh: THREE.Mesh | THREE.Group;
    body: CANNON.Body;
}


export class PhysicsBodyCreator {

    private static instance: PhysicsBodyCreator | undefined = undefined;

    private constructor() {/** */ };

    private mu = ModelUtils.getInstance();

    public static getInstance = () => {
        if (this.instance === undefined) {
            this.instance = new PhysicsBodyCreator();
        }
        return this.instance;
    }


    /**
     * Creates and returns physics objects of `multi-material objects`.
     */
    public createPoFromMmo = (glTFScene: THREE.Group) => {

        const mmo = this.mu.getMultiMaterialsObjects(glTFScene);
        const po: PGObject[] = [];
        mmo.map(g => {
            if (g.isGroup) {
                const body = MMOBodyCreator.getInstance().createBody(g);
                po.push({ mesh: g, body });
            }
        });
        return po;
    }

    /**
     * Creates and returns physics objects of `single-material objects`.
     */
    public createPoFromSmo = (glTFScene: THREE.Group) => {
        const sm = this.mu.getSingleMaterialObjects(glTFScene);
        const po: PGObject[] = [];
        sm.map(mesh => {
            if (mesh.isMesh) {
                const body = SMOBodyCreator.getInstance().createBody(mesh);
                po.push({ mesh, body });
            }
        });
        return po;
    }

}

