import chroma from "chroma-js";
import { Looper, LooperCallback } from "../core/Looper";

export class Draw2D {
    public canvas: HTMLCanvasElement;

    public context: CanvasRenderingContext2D;

    public grid = {
        enabled: true,
        nGx: 8,
        nGy: 8,
        lineColor: chroma("#888").alpha(0.7).hex(),
        lineWidth: 0.7,
        lineDash: [5, 7],
    };

    constructor(canvas: HTMLCanvasElement) {
        this.canvas = canvas;
        this.context = canvas.getContext("2d", {
            alpha: true,
        }) as CanvasRenderingContext2D;
        if (this.context === null) {
            throw new Error(`** Cannot get context 2d of the canvas ${canvas.id} ** `);
        }
    }

    public getWidth = () => this.canvas.width;
    public getHeight = () => this.canvas.height;

    public cX = () => this.canvas.width / 2;
    public cY = () => this.canvas.height / 2;

    public gX = () => this.canvas.width / this.grid.nGx;
    public gY = () => this.canvas.height / this.grid.nGy;

    public clear = () => {
        this.canvas.width = this.canvas.width + 0;
        return this;
    };

    public fill = (color: string) => {
        const ctx = this.context;
        ctx.save();
        ctx.fillStyle = color;
        ctx.fillRect(0, 0, this.canvas.width, this.canvas.height);
        ctx.restore();
        return this;
    };

    private _updateCallback: LooperCallback = () => { };

    public start = (updateCallback: LooperCallback) => {
        Looper.getInstance().addListener((this._updateCallback = updateCallback));
        return this;
    };

    public stop = () => {
        Looper.getInstance().removeListener(this._updateCallback);
        return this;
    };

    public showGrids = (enabled: boolean) => {
        this.grid.enabled = enabled;
        return this;
    };

    public drawGrids = (nGx?: number, nGy?: number) => {
        if (this.grid.enabled === false) {
            return this;
        }


        this.grid.nGx = nGx || this.grid.nGx;
        this.grid.nGy = nGy || this.grid.nGy;

        const ctx = this.context;
        ctx.save();

        //
        const sx = this.gX();
        const sy = this.gY();
        const w = this.getWidth();
        const h = this.getHeight();

        //
        ctx.strokeStyle = this.grid.lineColor;
        ctx.setLineDash(this.grid.lineDash);
        ctx.lineWidth = this.grid.lineWidth;

        // Vertical grid lines
        for (let i = 1; i < this.grid.nGx; i++) {
            ctx.beginPath();
            const x = sx * i;
            ctx.moveTo(x, 0);
            ctx.lineTo(x, h);
            ctx.stroke();
        }
        // Horizontal grid lines
        for (let i = 1; i < this.grid.nGy; i++) {
            ctx.beginPath();
            const y = sy * i;
            ctx.moveTo(0, y);
            ctx.lineTo(w, y);
            ctx.stroke();
        }

        //
        ctx.restore();

        return this;
    };
}
