export interface LooperParams {
    frameCount: number;
    deltaTime: number;
}

export type LooperCallback = (params: LooperParams) => void;

export interface FrameCountListener {
    callback: LooperCallback;
    frameCount: number;
    repeatCount?: number;
}

export class Looper {
    private static instance?: Looper;

    private running = false;

    private animationId = 0;

    private frameCount = 0;

    private deltaTime = 0;

    private listeners: LooperCallback[] = [];

    private constructor() {
        /* */
    }

    public static getInstance = () => {
        if (!this.instance) {
            this.instance = new Looper();
            this.instance.start();
        }
        return this.instance;
    };

    public addListener = (listener: LooperCallback) => {
        if (this.listeners.indexOf(listener) === -1) {
            this.listeners.push(listener);
        }
        return this;
    };

    public removeListener = (listener: LooperCallback) => {
        const index = this.listeners.indexOf(listener);
        if (index !== -1) {
            this.listeners.splice(index, 1);
        }
        return this;
    };

    private _t0 = 0;
    private _dts: number[] = [];
    private loop = () => {
        if (this.running === false) {
            return;
        }

        this.animationId = requestAnimationFrame(this.loop);
        this.frameCount += 1;

        const ms = performance.now();
        const dt = (ms - this._t0) / 1000;
        this._t0 = ms;

        this._dts.push(dt);
        if (this._dts.length > 10) {
            this._dts.shift();
        }

        this.deltaTime =
            this._dts.reduce((a: number, b: number) => {
                return a + b;
            }, 0) / this._dts.length;

        const data = { frameCount: this.frameCount, deltaTime: this.deltaTime };
        this.listeners.forEach((callback) => {
            callback(data);
        });

        //>> <<
        this._execFrameCount(data);
    };

    public start = () => {
        if (this.running === false) {
            this.animationId = requestAnimationFrame(this.loop);
        }
        this.running = true;
        this._t0 = performance.now();
        this.frameCount = 0;
        this.deltaTime = 0;
        return this;
    };

    public stop = () => {
        this.running = false;
        cancelAnimationFrame(this.animationId);
        this.listeners = [];
        return this;
    };

    public pause = () => {
        this.running = false;
        return this;
    };

    public resume = () => {
        this.running = true;
        this.animationId = requestAnimationFrame(this.loop);
        return this;
    };

    public onFrame = (callback: LooperCallback) => {
        this.addListener(callback);
        return this;
    };

    private _frameCountListeners: FrameCountListener[] = [];
    public onFrameCount = (callback: LooperCallback, frameCount: number, repeatCount?: number): (() => void) => {
        const listener = { callback, frameCount, repeatCount };
        if (this._frameCountListeners.indexOf(listener) === -1) {
            this._frameCountListeners.push(listener);
            // console.log(`listener add: ${this._frameCountListeners.length}`);
        }
        return () => {
            const index = this._frameCountListeners.indexOf(listener);
            this._frameCountListeners.splice(index, 1);
            // console.log(`listener remove: ${this._frameCountListeners.length}`);
        };
    };

    private _execFrameCount = (params: LooperParams) => {
        let removeIndex = -1;
        this._frameCountListeners.forEach((listener) => {
            if (this.frameCount % listener.frameCount === 0) {
                listener.callback(params);
                if (listener.repeatCount !== undefined && listener.repeatCount > 0) {
                    listener.repeatCount -= 1;
                    if (listener.repeatCount <= 0) {
                        removeIndex = this._frameCountListeners.indexOf(listener);
                    }
                }
            }
        });

        if (removeIndex >= 0) {
            this._frameCountListeners.splice(removeIndex, 1);
            //console.log(`listener remove*: ${this._frameCountListeners.length}`);
        }
    };
}
