import gsap from "gsap";

export interface BFSTween {
    startValue: number;
    endValue: number;
    ease?: gsap.EaseFunction | string;
    duration?: number;
    repeat?: number;
    yoyo?: boolean;
    onUpdate: (value: number) => void;
    onComplete?: () => void;
}

export const createTween = (options: BFSTween) => {
    const opt = options;
    const progress: { value: number } = { value: opt.startValue };
    return gsap.to(progress, {
        ease: opt.ease ? opt.ease : "elastic",
        value: opt.endValue,
        duration: opt.duration ? opt.duration : 1,
        repeat: opt.repeat === undefined || opt.repeat == 0 ? 0 : opt.repeat, // -1 infinite
        yoyo: opt.yoyo === true ? opt.yoyo : false,
        onUpdate: () => opt.onUpdate(progress.value),
        onComplete: () => opt.onComplete?.(),
    });
};
