import chroma from "chroma-js";
import { Draw2D } from "./Draw2D";

export class Plotter extends Draw2D {
    private props = {
        maxPoints: 100,
        min: -1,
        max: 1,
    };

    private array: number[] = [];

    constructor(canvas: HTMLCanvasElement) {
        super(canvas);
    }

    public addPoint = (data: number) => {
        this.props.max = data > this.props.max ? data : this.props.max;
        this.props.min = data < this.props.min ? data : this.props.min;

        this.array.push(data);
        if (this.array.length > this.props.maxPoints) {
            this.array.shift();
        }
        return this;
    };

    public plot = (data: number | number[]) => {
        Array.isArray(data) ? data.forEach((d) => this.addPoint(d)) : this.addPoint(data);

        const w = this.getWidth();
        const h = this.getHeight();
        const offset = h / 2;
        const n = this.props.maxPoints;
        const sx = w / (n - 1);

        const xyPoints: { x: number; y: number }[] = [];
        for (let i = 0; i <= n; i++) {
            const x = i * sx;
            const y = 5 * this.array[i] + offset;
            xyPoints.push({ x, y });
        }

        const ctx = this.context;
        ctx.save();
        this.clear();
        this.drawGrids();
        ctx.strokeStyle = chroma("red").alpha(0.5).hex();
        ctx.lineWidth = 3;
        ctx.beginPath();
        ctx.moveTo(xyPoints[0].x, xyPoints[0].y);
        for (let i = 1; i < xyPoints.length; i++) {
            const x = xyPoints[i].x;
            const y = xyPoints[i].y;
            ctx.lineTo(x, y);
        }
        ctx.stroke();
        ctx.restore();

        return this;
    };
}
