import * as THREE from "three";
import * as CANNON from "cannon-es";
import { SMOBodyCreator } from "./SMOBodyCreator";



export class MMOBodyCreator {

    private static instance: MMOBodyCreator | undefined = undefined;

    private constructor() {/* */ }


    public static getInstance = () => {
        if (this.instance == undefined) {
            this.instance = new MMOBodyCreator();
        }
        return this.instance;
    }


    /**
     * Return a bounding box of the multi-material object.
     */
    public getBoundingBox = (group: THREE.Group) => {

        const boxes: THREE.Box3[] = [];

        group.children.map(m => {

            /**
             * Method 1:
             */

            /*
            const mesh = m as THREE.Mesh;
            const arr: THREE.TypedArray = mesh.geometry.attributes.position.array;
            const positions: THREE.Vector3[] = [];
            for (let i = 0; i < arr.length; i += 3) {
                const v = arr[i];
                const p = new THREE.Vector3(arr[i + 0], arr[i + 1], arr[i + 2])
                positions.push(p)
            };
    
    
            let min: THREE.Vector3 = new THREE.Vector3(1e5, 1e5, 1e5);
            let max: THREE.Vector3 = new THREE.Vector3(1e5, 1e5, 1e5).multiplyScalar(-1);
            positions.forEach((v3) => {
                min.x = v3.x < min.x ? v3.x : min.x;
                min.y = v3.y < min.y ? v3.y : min.y;
                min.z = v3.z < min.z ? v3.z : min.z;
    
                max.x = v3.x > max.x ? v3.x : max.x;
                max.y = v3.y > max.y ? v3.y : max.y;
                max.z = v3.z > max.z ? v3.z : max.z;
            });
            const bb = new THREE.Box3(min, max);
            boxes.push(bb);
            */

            /**
             * Method 2.
             */
            const mesh = m as THREE.Mesh;
            const bb = new THREE.Box3();
            bb.min = mesh.geometry.boundingBox?.min as THREE.Vector3;;
            bb.max = mesh.geometry.boundingBox?.max as THREE.Vector3;;
            boxes.push(bb);
        });


        let min: THREE.Vector3 = new THREE.Vector3(1e5, 1e5, 1e5);
        let max: THREE.Vector3 = new THREE.Vector3(1e5, 1e5, 1e5).multiplyScalar(-1);
        boxes.forEach((box) => {
            let v3 = box.min;
            min.x = v3.x < min.x ? v3.x : min.x;
            min.y = v3.y < min.y ? v3.y : min.y;
            min.z = v3.z < min.z ? v3.z : min.z;

            v3 = box.max;
            max.x = v3.x > max.x ? v3.x : max.x;
            max.y = v3.y > max.y ? v3.y : max.y;
            max.z = v3.z > max.z ? v3.z : max.z;
        });

        const bb = new THREE.Box3(min, max);
        return bb;
    }

    public getRadiusFromBB = (box: THREE.Box3) => {
        let min = [box.min.x, box.min.y, box.min.z];
        let max = [box.max.x, box.max.y, box.max.z];
        let r = 0;
        [...min, ...max].forEach(v => {
            const len = Math.abs(v);
            r = len > r ? len : r;
        });
        return r;
    }

    public getBoundingBoxDimension = (box3: THREE.Box3) => {
        const dx = (box3.max.x - box3.min.x);
        const dy = (box3.max.y - box3.min.y);
        const dz = (box3.max.z - box3.min.z);
        return new CANNON.Vec3(dx, dy, dz);
    }

    public getCenterOfBoundingBox = (bb: THREE.Box3) => {

        const origin = new CANNON.Vec3();

        if (bb) {
            origin.x = bb.min.x + (bb.max.x - bb.min.x) / 2;
            origin.y = bb.min.y + (bb.max.y - bb.min.y) / 2;
            origin.z = bb.min.z + (bb.max.z - bb.min.z) / 2;
        }

        return new CANNON.Vec3(origin.x, origin.y, origin.z);

    }

    public getMassFromBoundingBox = (bb: THREE.Box3, scale: number = 1) => {
        const dim = this.getBoundingBoxDimension(bb);
        return (dim.x * dim.y * dim.z) * scale;
    }

    /**
     * SPHERE BODY
     * Create a Sphere body and attach a Sphere shape to it.
     */
    public createSphereBody = (group: THREE.Group) => {
        const bb = this.getBoundingBox(group);
        const radius = this.getRadiusFromBB(bb) * 1.0;
        const shape = new CANNON.Sphere(radius);
        const body = new CANNON.Body();
        const bc = SMOBodyCreator.getInstance();
        bc.copyPositionAndQuaternion(body, group);
        body.addShape(shape);
        body.mass = (4 / 3) * Math.PI * radius * radius * radius;   // V=4/3(r3)
        body.type = CANNON.Body.DYNAMIC;
        return body;
    }


    /**
     * CUBE BODY
     * Create a Cube body and attach a Box shape to it.
     */
    public createCubeBody = (group: THREE.Group) => {
        const bb = this.getBoundingBox(group);
        const halfExt = this.getRadiusFromBB(bb);
        const shape = new CANNON.Box(new CANNON.Vec3(halfExt, halfExt, halfExt));
        const body = new CANNON.Body();

        const bc = SMOBodyCreator.getInstance();
        bc.copyPositionAndQuaternion(body, group);
        body.addShape(shape);
        const dm = this.getBoundingBoxDimension(bb);
        body.mass = body.mass = dm.x * dm.y * dm.z;
        body.type = CANNON.Body.DYNAMIC;
        return body;
    }


    /**
     * CYLINDER BODY 
     * Create a Cylinder body and attach a Cylinder shape to it.
     */
    public createCylinderBody = (group: THREE.Group) => {
        const bb = this.getBoundingBox(group);
        const dm = this.getBoundingBoxDimension(bb);
        const radiusTop = dm.x * 0.5;
        const radiusBottom = dm.z * 0.5;
        const height = dm.y;
        const shape = new CANNON.Cylinder(radiusTop, radiusBottom, height, 16);

        const body = new CANNON.Body();

        const bc = SMOBodyCreator.getInstance();
        bc.copyPositionAndQuaternion(body, group);
        body.addShape(shape, this.getCenterOfBoundingBox(bb));
        body.mass = (Math.PI * radiusBottom * radiusBottom * height);
        body.type = CANNON.Body.DYNAMIC;
        return body;
    }

    /**
     * CONE BODY
     * Create a Cone body and attach a Cone shape to it.
     */
    public createConeBody = (group: THREE.Group) => {
        const bb = this.getBoundingBox(group);
        const dm = this.getBoundingBoxDimension(bb);
        const radiusTop = 0;
        const radiusBottom = dm.z * 0.5;
        const height = dm.y;
        const shape = new CANNON.Cylinder(radiusTop, radiusBottom, height, 16);

        const body = new CANNON.Body();

        const bc = SMOBodyCreator.getInstance();
        bc.copyPositionAndQuaternion(body, group);
        body.addShape(shape, this.getCenterOfBoundingBox(bb));
        body.mass = (Math.PI * radiusBottom * radiusBottom * height) * 0.5;
        body.type = CANNON.Body.DYNAMIC;
        return body;
    }

    /**
     * TORUS BODY
     * Create Torus body and attach a simple Cylinder shape to it.
     */
    public createTorusBody = (group: THREE.Group) => {
        return this.createCylinderBody(group);
    }

    public createCapsuleBody = (group: THREE.Group) => {
        const bb = this.getBoundingBox(group);
        const dims = this.getBoundingBoxDimension(bb);
        const capsuleRadius = dims.x / 2; // x and z should be the same.
        const capsuleHeight = dims.y - 2 * capsuleRadius;

        const cylinderShape = new CANNON.Cylinder(capsuleRadius, capsuleRadius, capsuleHeight, 16);

        const topSphereShape = new CANNON.Sphere(capsuleRadius);
        const bottomSphereShape = new CANNON.Sphere(capsuleRadius);

        const body = new CANNON.Body();

        const origin: CANNON.Vec3 = this.getCenterOfBoundingBox(bb);

        /**
         * Fine turning for position of the top and bottom shapes (spheres).
         */
        const yOffset = 0;
        const offset = new CANNON.Vec3(0, yOffset, 0);

        const topPos = origin.vadd(new CANNON.Vec3(0, +capsuleHeight / 2, 0));
        const botPos = origin.vadd(new CANNON.Vec3(0, -capsuleHeight / 2, 0));
        body.addShape(cylinderShape, origin);
        body.addShape(topSphereShape, topPos.vadd(offset));
        body.addShape(bottomSphereShape, botPos.vsub(offset));

        const bc = SMOBodyCreator.getInstance();
        bc.copyPositionAndQuaternion(body, group);
        body.mass = (Math.PI * capsuleRadius * capsuleRadius * capsuleHeight) + 1 * ((4 / 3) * Math.PI * capsuleRadius * capsuleRadius * capsuleRadius);
        body.type = CANNON.Body.KINEMATIC;
        return body;
    }

    /**
     * PLANE BODY
     * Create a Plane body and attach a Box shape to it.
     */
    public createPlaneBody = (group: THREE.Group) => {
        const bb = this.getBoundingBox(group);
        const dimes = this.getBoundingBoxDimension(bb);
        const arr = [dimes.x, dimes.y, dimes.z];
        arr.forEach(a => {
            a = a <= 0 ? 0.01 : a;
        });
        const half = new CANNON.Vec3(arr[0], arr[1], arr[2]).scale(0.5);

        const shape = new CANNON.Box(new CANNON.Vec3(half.x, half.y, half.z));
        const body = new CANNON.Body();
        body.quaternion.setFromAxisAngle(new CANNON.Vec3(1, 0, 0), -Math.PI / 2);

        body.addShape(shape, this.getCenterOfBoundingBox(bb));
        const bc = SMOBodyCreator.getInstance();
        bc.copyPositionAndQuaternion(body, group);
        body.mass = half.x * half.y * half.z;
        body.type = CANNON.Body.DYNAMIC;
        return body;
    }


    /**
     * Creates and return MultiShapesBody based on name of group object.
     */
    public createBody = (group: THREE.Group) => {

        const bc = SMOBodyCreator.getInstance();

        const name = group.name;

        let body: CANNON.Body | undefined = undefined;


        if (bc.isModelOf(name, "sphere") || bc.isModelOf(name, "ball") || bc.isModelOf(name, "icosphere")) {
            body = this.createSphereBody(group);
        }
        else if (bc.isModelOf(name, "cube") || bc.isModelOf(name, "box")) {
            body = this.createCubeBody(group);
        }
        else if (bc.isModelOf(name, "cylinder")) {
            body = this.createCylinderBody(group);
        }
        else if (bc.isModelOf(name, "cone")) {
            body = this.createConeBody(group);
        }
        else if (bc.isModelOf(name, "torus")) {
            body = this.createTorusBody(group);
        }
        else if (bc.isModelOf(name, "capsule")) {
            body = this.createCapsuleBody(group);
        }
        else if (bc.isModelOf(name, "plane") || bc.isModelOf(name, "ground") || bc.isModelOf(name, "floor") || bc.isModelOf(name, "wall")) {
            body = this.createPlaneBody(group);
            SMOBodyCreator.getInstance().makeStaticBody(body);
        }
        else {
            console.log(`UNKNOWN MODEL TYPE`);
        }

        if (body) {
            body.updateMassProperties();
            body.aabbNeedsUpdate = true;
        }
        else {
            throw new Error("** The body is not created! **");
        }

        return body;
    }
}