import * as THREE from "three";

export class ModelUtils {

    private static instance: ModelUtils | undefined = undefined;

    private constructor() {/** */ }

    public static getInstance = () => {
        if (this.instance === undefined) {
            this.instance = new ModelUtils();
        }
        return this.instance;
    }


    /**
     * Returns `true` if the `obj` is `face` objet (one of group children).
     */
    public isFace = (obj: THREE.Object3D) => {
        /**
         * Conditions:
         * 
         * Type:        THREE.Mesh
         * Parent:      THREE.Group
         * 
         * Parent has more 2 or more children
         * The children has the same name prefix e.g., c, c_1, c_2
         */

        if ((obj instanceof THREE.Mesh) && (obj.parent instanceof THREE.Group)) {
            const parent = obj.parent;
            if (parent.children.length >= 2) {
                const namePrefix = parent.children[0].name.split('_')[0]; // name: abc, abc_1, abc_2
                let cnt = 0;
                parent.children.forEach(c => {
                    if (c.name.indexOf(namePrefix) === 0) {
                        cnt += 1;
                    }
                });
                return cnt === parent.children.length;
            }
        }
        return false;
    }

    /**
     * Returns `true` if the `obj` is `multi-material object`.
     */
    public isMultiMaterialObject = (obj: THREE.Object3D) => {
        /**
         * Conditions:
         * 
         * Type of Group
         * Has 2 or more children
         * Children type is face
         * Children name prefix is the same
         * Parented to Scene or Group
         */
        if (obj instanceof THREE.Group) {
            const parent = obj.parent;
            if (parent instanceof THREE.Group || parent instanceof THREE.Scene) {
                const children = obj.children;
                let cnt = 0;
                if (children.length >= 2) {
                    children.forEach(c => {
                        if (this.isFace(c)) {
                            cnt += 1;
                        }
                    })
                }
                return cnt === children.length;
            }
            return false;
        }
    }


    /**
     * Returns `multi-material objects`, the children of the `parent`.
     */
    public getMultiMaterialsObjects = (parent: THREE.Scene | THREE.Group) => {
        const groups: THREE.Group[] = [];
        parent.children.forEach((c) => {
            if (this.isMultiMaterialObject(c)) {
                groups.push(c as THREE.Group);
            }
        });
        return groups;
    }


    /**
     * Returns `true` if the `obj` is `single-material object`.
     */
    public isSingleMaterialObject = (obj: THREE.Object3D) => {

        /**
         * Conditions:
         * 
         * Type is THREE.Mesh
         * It is not type of Face
         * It is parented to THREE.Group or THREE.Scene
         */

        const parent = obj.parent;

        if (obj instanceof THREE.Mesh && !this.isFace(obj)) {
            return parent instanceof THREE.Group || parent instanceof THREE.Scene;
        }
        return false;
    }


    /**
     * Returns `single-material objects`, children of the given parent.
     */
    public getSingleMaterialObjects = (glTFScene: THREE.Group) => {
        const meshes: THREE.Mesh[] = [];
        glTFScene.children.forEach((c) => {
            if (this.isSingleMaterialObject(c)) {
                meshes.push(c as THREE.Mesh);
            }
        });
        return meshes;
    }
}
