

export class CanvasUtils {


    private static instance: CanvasUtils;

    private constructor() {/** */ }

    public static getInstance(): CanvasUtils {
        if (this.instance === undefined) {
            this.instance = new CanvasUtils();
        }
        return this.instance;
    }


    public create = (idPrefix: string, zIndex: number, container?: HTMLElement) => {
        const canvas = document.createElement("canvas");
        if (container)
            container.appendChild(canvas);
        const uid = (Math.random() * 100000).toFixed(0);
        canvas.id = `${idPrefix}-${uid}`;
        canvas.style.position = "absolute";
        canvas.style.zIndex = `${zIndex}`;
        return canvas;
    }

}