import chroma from "chroma-js";
import { DevBox } from "./DevBox";



export class DevLog {

    private static instance: DevLog | undefined = undefined;

    private devBox: DevBox;

    private container: HTMLElement;

    private constructor() {
        //>> Main container
        this.container = document.createElement("div") as HTMLElement;
        this.container.className = `flex flex-col min-w-[100px] min-h-[10px]`;

        this.devBox = new DevBox().appendChild(this.container).setPosition(2, 65);
        this.devBox.appendChild(this.container).setSize(800, 20).autoSize();
        this.devBox.container.style.background = "none";
    }

    public static getInstance = () => {
        if (this.instance === undefined) {
            this.instance = new DevLog();
        }
        return this.instance;
    }

    public removeLine = (id: number) => {
        const target = Array.from(this.container.children).filter(d => d.id === `line-devLog-id-${id}`)[0];
        if (target) {
            this.container.removeChild(target);
        }
        return this;
    }

    public removeFirst = () => {
        if (this.container.children.length > 0) {
            const target = this.container.children[0];
            if (target) {
                this.container.removeChild(target);
            }
        }
        return this;
    }

    public removeLast = () => {
        if (this.container.children.length > 0) {
            const target = this.container.children[this.container.children.length - 1];
            if (target) {
                this.container.removeChild(target);
            }
        }
        return this;
    }


    private randomColor = () => {
        let color;
        do {
            color = chroma.random().brighten(0.5); // Increase lightness by 0.5 (0 to 1)
        } while (color.luminance() < 0.5); // Filter colors with a luminance less than 0.5 (adjust this threshold as needed)

        return color.hex();
    }

    private lastMessage = "";

    public log = (message: any, color?: string) => {

        if (message === this.lastMessage) {
            return this;
        }

        this.lastMessage = message;

        //>> container
        const lineWrapper = document.createElement("div");
        lineWrapper.className = `flex flex-row justify-left items-center space-y-[2px]`;


        //>> left
        const icL = document.createElement("div");
        icL.className = `bg-green-300 w-1 h-1 rounded-full`;

        //>> text
        const txt = document.createElement("span");
        txt.className = "flex-grow text-gray-400 text-[12px] mx-2";
        txt.innerText = message;
        txt.style.color = color || this.randomColor();// chroma.random().brighten(0.8).hex();//chroma.random().hex();


        //>> right
        const icR = document.createElement("div");
        icR.className = `bg-red-300 w-1 h-1 rounded-full`;

        lineWrapper.appendChild(icL);
        lineWrapper.appendChild(txt);
        lineWrapper.appendChild(icR);

        lineWrapper.id = `line-devLog-id-${this.container.childNodes.length}`;
        this.container.appendChild(lineWrapper);

        if (this.container.children.length > 10) {
            this.removeFirst();
        }
    }
}

